<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>

export default {
  methods: {
    async initWebMeta() {
      try {
        const state = this.$store.state
        const res = await this.$http.get('/api/getWebMeta', {})
        if (res.data.main_banner_lst && res.data.main_banner_lst.length > 0) {
          state.mainBannerList = res.data.main_banner_lst
        }
        if (res.data.sub_banner_lst && res.data.sub_banner_lst.length > 0) {
          state.subBannerList = res.data.sub_banner_lst
        }
        if (res.data.other_lst) {
          res.data.other_lst.forEach(item => {
            if (state.otherImgMap[item.key] && item.src) {
              state.otherImgMap[item.key] = item.src
            }
          })
        }
        console.log('===========================');
        console.log('initWebMeta 成功');
        console.log(state);
        console.log('===========================');
      } catch (err) {
        console.log('===========================');
        console.log('initWebMeta 失败');
        console.log(err);
        console.log('===========================');
      }
    }
  },
  created() {
    this.initWebMeta()
  }
}
</script>

<style scoped lang="less">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  max-width: 1920px;
  margin: 0 auto;
}
</style>
